function preloadImages(urls) {
  urls.forEach((url) => {
    const img = new Image();
    img.src = url;
  });
}
const imageUrls = ["../img/day-time-big.avif", "../img/nite-time-big.avif"];
window.onload = () => {
  preloadImages(imageUrls);
};

