// JUSTERAR BAKGRUNDEN ----------------------------------------------------------------------------
// ---------------------------------------------------------------------------- JUSTERAR BAKGRUNDEN

document.addEventListener("scroll", function () {
  let scrolledY = window.scrollY;
  let positionY = -scrolledY * 0.1;

  document.body.style.backgroundPosition = "center " + positionY + "px";
});

// ------------------------------------------------------------------------------------------------
// F√ÑRGTEMAT --------------------------------------------------------------------------------------
// Kollar om det finns n√•got i local storage annars systemets och jag styr detta med klasser som
// appliceras p√• body. Har en knapp som v√§xlar f√§rgtemat och byter ikon i knappen beroende p√• vad
// som √§r f√∂redraget. Kollar ocks√• efter √§ndringar i systemet , uppdaterar om anv√§ndaren skiftar.
// -------------------------------------------------------------------------------------- F√ÑRGTEMAT

document.addEventListener("DOMContentLoaded", () => {
  const themeToggler = document.getElementById("theme-toggler");
  const prefersDarkScheme = window.matchMedia("(prefers-color-scheme: dark)");

  const currentTheme = localStorage.getItem("theme")
    ? localStorage.getItem("theme")
    : null;

  if (currentTheme) {
    document.body.classList.add(currentTheme);
    updateButtonIcon(currentTheme);
  } else {
    updateThemeBody(prefersDarkScheme.matches);
  }

  themeToggler.addEventListener("click", () => {
    let theme = document.body.classList.contains("dark-theme")
      ? "light-theme"
      : "dark-theme";
    document.body.classList.remove("dark-theme", "light-theme");
    document.body.classList.add(theme);
    localStorage.setItem("theme", theme);
    updateButtonIcon(theme);
  });

  prefersDarkScheme.addEventListener("change", (e) => {
    updateThemeBody(e.matches);
  });

  function updateThemeBody(isDark) {
    const theme = isDark ? "dark-theme" : "light-theme";
    document.body.classList.remove("dark-theme", "light-theme");
    document.body.classList.add(theme);
    localStorage.setItem("theme", theme);
    updateButtonIcon(theme);
  }

  function updateButtonIcon(theme) {
    themeToggler.textContent = theme === "dark-theme" ? "‚òÄÔ∏è" : "üåë";
  }
});

// ------------------------------------------------------------------------------------------------
// Hover p√• temaknapp -----------------------------------------------------------------------------
// ----------------------------------------------------------------------------- Hover p√• temaknapp

const button = document.getElementById("theme-toggler");

button.addEventListener("mouseenter", function () {
  if (this.textContent === "‚òÄÔ∏è") {
    this.textContent = "üåû";
  } else if (this.textContent === "üåë") {
    this.textContent = "üåö";
  }
});

button.addEventListener("mouseleave", function () {
  if (this.textContent === "üåû") {
    this.textContent = "‚òÄÔ∏è";
  } else if (this.textContent === "üåö") {
    this.textContent = "üåë";
  }
});
